.content {
    margin: 200px auto;
    padding-top: 50px;
    width: 400px;
    height: 300px;
    border: #61dafb solid 2px;
    border-radius: 20px;
    background-color: dimgray;
}
.buttons{
    border-radius: 15px;
    margin-top: 10px;
    padding: -20px;
    display: inline-block;
    width: 300px;
    height: 150px;
    border: 2px solid #61dafb;
    background-color: darkcyan;
}
.buttonReset{
    border-radius: 5px;
    height: 40px;
    width: 90px;
    color: red;
    font-size: 20px;
}
.buttonReset{
    cursor: pointer;
}
